<template>
    <d2-container>
        <!-- 销售管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="24" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="Banner管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="5">
                    <!-- <el-button type="primary" plain>查询</el-button> -->
                    <!-- <el-button type="primary" plain @click="add_good">添加</el-button> -->
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">

            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="name" label="名称" width="180">
            </el-table-column>
            <el-table-column label="角色" width="180">
                <template slot-scope="a">
                    <p>销售</p>
                </template>
            </el-table-column>
            <el-table-column prop="is_phone" label="手机号" width="180">
            </el-table-column>
            <el-table-column prop="email" label="邮箱">
            </el-table-column>
            <el-table-column label="图片">
                <template slot-scope="a">
                    <img :src="a.row.qrcode_address" alt="" style="width: 100px;">
                </template>
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <!-- <el-button type="primary" plain @click="edit_type(val.row)">编辑</el-button> -->
                    <el-button type="primary" plain @click="init_goods(val.row)">查看员工</el-button>
                    <!-- <el-button type="danger" plain @click="del_val(val.row)">删除</el-button> -->
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <!-- <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row> -->

    </d2-container>

</template>

<script>
    import { show_goods_type, roles, all_roles } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'market_home',
        data() {
            return {
                num: '',
                value3: '',
                state1: '',
                checked: 0,
                tableData: [],
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            page(a) {
                console.log(a);
                this.pages = a;
            },
            init_goods(a) {
                console.log(a);
                this.$router.push({path:"/market_child",query:{id:a.id,name:a.name}})
            },
        },
        mounted() {
            const val = async () => {
                let list = [];
                const main = await roles().then(res => {
                    let val = {}
                    res.data.forEach(ele => {
                        if (ele.name == "market") {
                            return val = ele
                        }
                    })
                    return val
                })
                const user = await all_roles(main.id).then(res => {
                    this.tableData = res.data
                    return list = res.data
                })
                return list
            }
            val()
        }
    }
</script>
<style lang="scss">
</style>